@mixin font($fs:16px, $fw:700, $lh:32px, $ls:0.01em) {
    font-size: $fs;
    font-weight: $fw;
    line-height: $lh;
    letter-spacing: $ls;
}

@mixin flex_justify_align {
    display: flex;
    justify-content: center;
    align-items: center;
}

@mixin flex_align {
    display: flex;
    align-items: center;
}

@mixin desk {
    @media screen and (min-width: 1440px) {
        @content;
    }
}

@mixin tab {
    @media screen and (min-width: 768px) {
        @content;
    }
}

@mixin mob-only {
    @media screen and (max-width: 1439px) {
        @content;
    }
}

@mixin mob {
    @media screen and (min-width: 391px) {
        @content;
    }
}

@mixin change_to_white_with_pointer {
    transition: all 250ms cubic-bezier(0.4, 0, 0.2, 1);
    &:hover {
        cursor: pointer;
        color: var(--clearly-white);
    }
}

@mixin change_to_grey_with_pointer {
    transition: all 250ms cubic-bezier(0.4, 0, 0.2, 1);
    &:hover {
        cursor: pointer;
        color: var(--uniswap-grey);
    }
}

@mixin scale_with_pointer {
    transition: all 250ms cubic-bezier(0.4, 0, 0.2, 1);
    &:hover {
        cursor: pointer;
        transform: scale(1.05);
    }
}

@mixin focus_scale_with_pointer {
    transition: all 250ms cubic-bezier(0.4, 0, 0.2, 1);
    &:focus {
        cursor: pointer;
        transform: scale(1.03);;
    }
}
@mixin hover_focus_background_dark_pink {
    &:hover,
    &:focus {
        background-color: var(--uniswap-dark-pink);
    }
}